:root {
  /*กำหนดตัวแปรและค่า*/
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.6rem;
  --typing-font: 3rem;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  width: 100vw;  
  min-height: 100vh;
  /*กว้างยาวเต็มจอ*/
}

nav {
  padding: 60px, 30px 0px 30px;
  flex: var(--sidebar-flex);
  background: #030B16;
}

nav ul {
  font-size: var(--fontsize-nav);
  text-align: center;
  list-style: none;
}

nav ul li {
  margin: 40px;
}

nav ul li a {
  color : #FFFFFF;
  text-decoration: none;
}

.condiv {
  padding: 100px;
  background-color :#f4f5f9;
  flex: var(--condiv-flex)
}

.home { 
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.profilepic {
  border-radius: 50%;
  width: 400px;
  margin: 30px;
}

.social {
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}

.social i {
  padding-right: 30px;
  font-size: 25px;
  color : #9a9b9c;
}

.social i:hover {
  color : #333333;
}

.typingeffect {
  font-size: 50px;
  margin-bottom: 30px;
}